/* ===================================
========= register page  */
.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  background-image: url("https://images.unsplash.com/photo-1515377905703-c4788e51af15?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MjB8fGVzc2VudGlhbCUyMG9pbHN8ZW58MHx8MHx8fDA%3D&auto=format&fit=crop&w=800&q=60");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-family: "Poppins", sans-serif;
}

.wrapper {
  width: 420px;
  background: transparent;
  border: solid 2px rgba(255, 255, 255, .2);
  backdrop-filter: blur(15px);
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
  color: white;
  border-radius: 1rem;
  padding: 30px 40px;
}

.wrapper h1 {
  font-size: 36px;
  text-align: center;
  font-weight: bolder;
}

.wrapper .input-box {
  width: 100%;
  height: 50px;
  margin: 30px 0;
}

.input-box input {
  position: absolute;
  height: 50px;
  width: 340px;
  background: transparent !important;
  border: none;
  outline: none;
  border: 2px solid rgba(255, 255, 255, .2);
  border-radius: 40px;
  font-size: 16px;
  color: white;
  padding: 20px 45px 20px 20px;
}

.input-box textarea {
  width: 340px;
  background: transparent !important;
  border: none;
  outline: none;
  border: 2px solid rgba(0, 0, 0, .2);
  border-radius: 20px;
  font-size: 16px;
  color: black;
  padding: 20px 45px 20px 20px;
}

.input-box input::placeholder {
  color: white;
}

.input-box i {
  right: 20px;
  font-size: 20px;
  color: white !important;
}

.icons {
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: end;
  align-items: center;
}

.remember-forget {
  display: flex;
  justify-content: space-between;
  font-size: 14.5px;
  margin: -15px 0 15px;
}

.remember-forget label input {
  accent-color: white;
  margin-right: 3px;
}

.remember-forget a {
  color: white;
  text-decoration: none;
}

.remember-forget a:hover {
  color: gold;
  text-decoration: underline;
  cursor: pointer;
}

.wrapper .btn1 {
  width: 100%;
  height: 45px;
  background: white;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
  cursor: pointer;
  font-size: 16px;
  color: black;
  font-weight: 600;
}

.btn1:hover {
  background-color: gold;
  color: black;
  transition: .3s;
}

.register-link {
  font-size: 14.5px;
  text-align: center;
  margin-top: 20px;
}

.register-link p {
  font-weight: lighter;
}

.register-link p a {
  color: white;
  text-decoration: none;
  font-weight: 500;
}

.register-link p a:hover {
  color: gold;
  text-decoration: underline;
}

.bgp {
  background-image: none !important;
  min-height: 70vh !important;
}

.bgp .wrapper .input-box input{
  color: black !important;
  border: 2px solid rgba(0, 0, 0, .2);
}

.bgp .wrapper .input-box input::placeholder {
  color: black !important;
}

.bgp .wrapper .btn1 {
  background-color: #14390f !important;
  color: white !important;
}

.bgp .wrapper .btn1:hover {
  color: gold !important;
}