@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

#navbar {
  padding: 0;
}

.fixed-navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* //navbar css */
.navbar {
  width: 100%;
  background-color: #14390f;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  /* height: 50px !important; */
  /* line-height: 26px; */
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
}

.nav-top {
  background-color: #EDF5E1;
  height: 150px;
} 

.nav-top ul {
  list-style: none;
}

.nav-top ul li a {
  font-size: 1.2rem;
  color: #14390f !important;
}

.nav-top ul li a:hover {
  color: gold !important;
  cursor: pointer;
  transition: .3s;
}

.ulm li {
  padding: 20px !important;
}

.ad .active {
  color: gold !important;
  background-color: #14390f !important;
  border: none !important;
  border-radius: 0 !important;
}

.brr {
  border-radius: 0 !important;
}

.navbar-toggler {
  border: none !important;
  padding: 0 !important;
}

.navbar-toggler:focus, .navbar-toggler:active {
  box-shadow: none !important;
  border: none !important;
}

.wid {
  width: 260px !important;
}

#search-bar {
  border: solid 1px #14390f;
  border-right: none !important;
  width: 380px;
  border-top-left-radius: .8rem;
}

#search-bar:focus {
  box-shadow: none;
  /* border: 1px solid #05386B; */
}

.search-btn {
  border: solid 1px #14390f;
  border-left: none !important;
  color: black !important;
  border-bottom-right-radius: .8rem !important;
  background-color: white !important;
}

.search-btn:hover {
  border: solid 1px #14390f;
  border-left: none !important;
  background-color: gold !important;
  color: black !important;
  transition: .2s !important;
}

.sf {
  max-width: 300px;
}

.nav-link {
  font-weight: bolder !important;
  font-family: sans-serif;
  font-size: smaller !important;
  color: white !important;
}

/* .active {
  border-bottom: 2px solid #05386B;
} */

.navbar-brand {
  font-weight: 700;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  letter-spacing: 3px;
  color: #05386B;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
  /* max-width: 250px !important; */
}
.search-form input {
  border: none;
  border-radius: 0;
}

.search-form button {
  background-color: #000000;
  border-radius: 0;
  color: white;
}

.logo {
  max-width: 200px;
  border-radius: 50%;
}

/* =============================
==========footer============= */

footer {
  position: relative;
  width: 100%;
  background: black;
  min-height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

footer hr {
  color: white;
  width: 80%;
}

footer .social-icons, footer .menu-links {
  padding: 0 !important;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  flex-wrap: wrap;
}

footer .social-icons li, footer .menu-links li {
  list-style: none;
}

footer .social-icons li a {
  font-size: 1.8em;
  color: white;
  margin: 0 20px;
  display: inline-block;
  transition: .5s;
}

footer .social-icons li .insta:hover {
  transform: translateY(-10px);
  color: #d62976;
}

footer .social-icons li .faceb:hover {
  transform: translateY(-10px);
  color: #466bbb;
}

footer .social-icons li .whats:hover {
  transform: translateY(-10px);
  color: #25d366;
}

footer .social-icons li .gMail:hover {
  transform: translateY(-10px);
  color: red;
}

footer .menu-links li a {
  font-size: 1.2em;
  color: white !important;
  margin: 0 35px;
  display: inline-block;
  text-decoration: none;
  opacity: .75;
  cursor: pointer;
}

footer .menu-links li a:hover {
  transition: .5s;
  opacity: 1;
  color: gold !important;
  text-decoration: underline !important;
}

footer p {
  color: white;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 10px;
  font-size: 1.1em;
}

