/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bcp {
  background-image: none !important;
}

.bcp .wrapper textarea {
  color: white;
}

.contactus .wrapper input::placeholder, .contactus .wrapper textarea::placeholder {
  color: white !important;
}

.bcp .wrapper .btn1 {
  background-color: #14390f !important;
  color: white !important;
}

.bcp .wrapper .btn1:hover {
  color: gold !important;
}

.bcp .wrapper .input-box textarea {
  border: 2px solid rgba(255, 255, 255, .2);
}

.cnthead {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: white;
}

.cntp {
  font-weight: bold;
}

.abttext {
  padding: 50px 100px 50px 100px;
  margin-bottom: 100px;
  background-image: linear-gradient(to bottom right , #14390f, gold);
  border-radius: 1rem;
  color: white;
}

.abttext h1 {
  color: white;
}

.backlg {
  background-image: linear-gradient(to bottom right , #14390f, gold);
  border-radius: 1rem;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  font-weight: 700 !important;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: #14390f;
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #14390f !important;
  color: gold !important;
  transition: 0.3s !important;
}

