@import url('https://fonts.googleapis.com/css2?family=Merriweather&family=Montserrat&family=Poppins:wght@900&family=Sacramento&display=swap');

.home-page h1 {
  color: #14390f;
  font-family: 'Poppins', sans-serif;
  font-weight: 800;
  margin-bottom: 25px;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  background-color: #EDF5E1;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card-price {
  color: #14390f;
  font-weight: bold;
  font-size: 15px;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

/* .home-page .card img:hover {
  transform: scale(0.9);
} */

.home-page .card-img-top {
  height: 300px !important;
}
