.loadmore {
  color: black;
  font-weight: bold;
  font-family: 'Times New Roman', Times, serif;
  font-size: 1rem !important;
}

.iMG-img {
  height: 300px;
  width: 450px;
}

.txt {
  height: 300px;
  width: 450px;
  background-color: #14390f;
}

.txt p {
  color: white;
  font-size: medium;
  font-family: 'Times New Roman', Times, serif;
}

.rp {
  padding: 0 20px;
  max-width: 1050px;
}

.home-text {
  padding: 0 150px;
  margin-top: 80px;
  margin-bottom: 60px;
}

.home-text h3 {
  color: #14390f;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 30px;
  margin-bottom: 30px;
}

.home-text h5 {
  color: #14390f;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 10px;
  margin-top: 30px;
}

.hp {
  font-weight: bold;
}

.hpr {
  margin-bottom: 10px !important;
}

.pcard {
  min-height: 463px !important;
  overflow: hidden !important;
}

.ct {
  font-size: large;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.cardp {
  font-size: small;
  font-weight: 500;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.phdText {
  background-color: #14390f;
  padding: 20px;
  text-align: center;
}

.phdText p {
  color: white;
  font-size: .9rem;
  margin: 0% !important;
}

.cur {
  border-radius: 1rem;
  overflow: hidden;
  margin-bottom: 50px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.lasth {
  font-weight: bolder;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin-bottom: 10px !important;
  font-size: 1.5rem;
}